import React from 'react';
import { Button } from 'reactstrap';

import './products.css';

import DB from '../db';

class ProductList extends React.Component {
  constructor(props) {
    super(props);

    this.onCategoryClick = this.onCategoryClick.bind(this);
    this.onAddToCart = this.onAddToCart.bind(this);

    this.state = {
      currentCategoryId: 0
    }
  }

  onCategoryClick(id, evt) {
    this.setState({ currentCategoryId: id });
  }

  onAddToCart(id, catId, evt) {
    console.log('catid ' + catId + ' id ' + id);
  }

  render() {
    let id = -1, itemid = -1;
    let categories = DB.map((item) => {
      ++id;

      return (
        <li key={id} className="category-item list-group-item d-flex justify-content-between lh-condensed" onClick = {this.onCategoryClick.bind(this, id)}>
          <div>
            <h6 className="my-0">{item.categoryName}</h6>
            <small className="text-muted">{item.description}</small>
          </div>
        </li>
      );
    });

    let items = DB[this.state.currentCategoryId].subProducts.map((item) => {
      ++itemid;

      return (
        <div key={itemid} className="mt-3 product-item">
          <div className="row mt-3">
            <div className="col-lg-2">
            </div>

            <div className="col-lg-8">
              <h5>Name: {item.name}</h5>
              <h6>Price: ${item.price}</h6>
            </div>

            <div className="col-lg-2">
              <h6>Quantity: {item.quantity}</h6>
              <Button color="primary" onClick={this.onAddToCart.bind(this, itemid, this.state.currentCategoryId)}>Add to Cart</Button>
            </div>
          </div>
        </div>
      )
    });

    return (
      <div className="container mt-3">
        <div className="row">
          <div className="col-lg-12">
            <h1 className="product-header">Our Products</h1>
          </div>
        </div>

        <div className="row">
          <div className="col-lg-4 mb-4">
            <ul className="list-group mb-3">
              {categories}
            </ul>
          </div>

          <div className="product-list col-lg-8 mb-4">
            {items}
            <div className="final-border mt-3"></div>
          </div>
        </div>

        <hr />
      </div>
    )
  }
}

export default ProductList;
